.appeals-container {
  font-size: 12px;
  height: calc(100vh - 100px);
  display: flex;
  flex-direction: column;
}

.column-toggle {
  margin-bottom: 15px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  font-size: 12px;
}

.toggle-label {
  margin-right: 10px;
  font-weight: bold;
}

.toggle-item {
  display: inline-flex;
  align-items: center;
  margin-right: 10px;
  margin-bottom: 5px;
  cursor: pointer;
}

.toggle-item input {
  margin-right: 3px;
}

.toggle-text {
  white-space: nowrap;
}

.table-responsive {
  flex: 1;
  overflow-y: auto;
  overflow-x: auto;
  padding-bottom: 35px; /* Отступ снизу */
}

/* Стили для скроллбара */
.table-responsive::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.table-responsive::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.table-responsive::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}

.table-responsive::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.appeals-table {
  width: 100%;
  border-collapse: collapse;
  font-size: 12px;
  margin-bottom: 20px; /* Дополнительный отступ для последней строки */
}

.appeals-table th {
  position: sticky;
  top: 0;
  background-color: #f2f2f2;
  z-index: 1;
}

.appeals-table th, .appeals-table td {
  border: 1px solid #ddd;
  padding: 5px;
  text-align: left;
}

.appeals-table th {
  background-color: #f2f2f2;
  font-weight: bold;
}

.appeals-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.appeals-table tr:hover {
  background-color: #f5f5f5;
}

/* Стили для Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #888 #f1f1f1;
}

/* Медиа-запрос для мобильных устройств */
@media (max-width: 768px) {
  .appeals-container {
    padding: 10px;
    height: calc(100vh - 80px);
  }

  .column-toggle {
    flex-direction: column;
    align-items: flex-start;
  }

  .toggle-label {
    margin-bottom: 5px;
  }

  .toggle-item {
    margin-bottom: 3px;
  }
}