.left-column {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    width: 230px;
    padding: 10px;
    background-color: #f8f9fa;
    border-right: 1px solid #dee2e6;
    box-sizing: border-box;
    overflow-y: auto;
}

.left-column-content {
    margin-bottom: 20px;
}

.left-column h2 {
    font-size: 1em;
    margin-bottom: 20px;
}

.left-column .button {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    padding: 10px;
    background-color: #6c757d;
    border-color: #6c757d;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    font-size: 12px;
    transition: background-color 0.3s;
    width: 100%;
    box-sizing: border-box;
}
.btn-active{
    background-color: #2196F3!important;

}

.left-column .button .icon {
    margin-right: 10px;
}

.left-column .button:hover {
    background-color: #5a6268;
}

.contact-links {
    margin-top: auto;
}

.contact-links h3 {
    font-size: 1.2em;
    margin-bottom: 10px;
}

.contact-links ul {
    list-style-type: none;
    padding: 0;
}

.contact-links li {
    margin-bottom: 10px;
}

.contact-links a {
    color: #6c757d;
    text-decoration: none;
    display: flex;
    align-items: center;
    transition: color 0.3s, text-shadow 0.3s;
}

.contact-links a .icon {
    margin-right: 10px;
}

.contact-links a:hover {
    color: #5a6268;
    text-shadow: 0 0 5px rgba(90, 98, 104, 0.7);
}

.equipment-modal {
    padding: 20px;
  }
  
  .equipment-list {
    max-height: 400px;
    overflow-y: auto;
    margin-top: 20px;
  }
  
  .equipment-item {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin-bottom: 8px;
    cursor: pointer;
    transition: background-color 0.2s;
  }

.error-message {
  color: #dc3545;
  margin-top: 10px;
  padding: 10px;
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 4px;
  font-size: 14px;
}

.button-disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.button-disabled:hover {
  background-color: initial;
}

.equipment-modal {
  padding: 20px;
}

.equipment-list {
  max-height: 400px;
  overflow-y: auto;
  margin-top: 20px;
}

.equipment-item {
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
  margin-bottom: 8px;
  cursor: pointer;
  transition: background-color 0.2s;
}

.equipment-item:hover {
  background-color: #f5f5f5;
}

.email-container {
  display: flex;
  align-items: center;
  gap: 8px;
}

.copy-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 4px;
  color: #666;
  position: relative;
}

.copy-button:hover {
  background: none;
}

.copy-button:hover .copy-icon {
  color: #666;
}

.copied-tooltip {
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
  margin-bottom: 4px;
}

.copy-icon {
  color: #666;
}


@media (max-width: 768px) {
    .left-column {
        width: 100%;
        padding: 10px;
        height: auto;
        overflow-y: visible;
    }
}





























